import styled from "styled-components";

const SickButton = styled.button`
  background: red;
  color: white;
  font-weight: 500;
  border: 0;
  border-radius: 0;
  text-transform: uppercase;
  font-size: 2rem;
  padding: 0.8rem 1.5rem;
  transform: skew(-2deg);
  display: inline-block;
  transition: all 0.5s;
  &[disabled],
  &:hover {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &[disabled="false"],
  &:hover {
    cursor: pointer;
  }
`;

export default SickButton;
